import axios from 'axios'
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    config: {}
  },
  mutations: {
    SET_CONFIG (state, config) {
      state.config = config
    },
  },
  actions: {
    async fetchConfig ({ commit }) {
      const response = await axios.get('apidocs-api/config')

      commit('SET_CONFIG', {})
    },

    fetchEndpoints () {

    }
  }
})
